  animation: circle 10s 10s infinite alternate;
  transform: rotate(0deg);
}

@keyframes circle {
  50% {
    border-radius: 100%;
    transform: rotate(360deg);
  }
}

.icon-fries, .icon-close {
  position: absolute;
  top: 50%;
